/* Focus */
.ug input:focus ~ .uh {
    box-shadow: 0 0 0 .075rem #fff, 0 0 0 .2rem #0074d9;
}

/* Checked state */
.ug input:checked ~ .uh {
    background-color: #3097d1;
    border-color: #3097d1;
    box-shadow: none;
}

/* Active */
.ug input:active ~ .uh {
    background-color: #6fb6df;
    border-color: #6fb6df;
    box-shadow: none;
}

.ug input:disabled ~ .uh {
    background-color: #f2f2f2;
    border-color: rgba(0, 0, 0, 0.15);
    box-shadow: none;
    cursor: not-allowed;
}

/* Checkbox modifiers */
.fk .uh {
    margin-top: 6px;
}

.uk .uh {
    border-radius: .33rem;
}

.uk input:checked ~ .uh {
    background-image: url(data:image/svg+xml;base64,);
}

/* Radio modifiers */
.ul .uh {
    border-radius: 50%;
}

.ul input:checked ~ .uh {
    background-image: url(data:image/svg+xml;base64,);
}

/* Alternately, use another character */
